import { Button } from 'ether-ui';
import { useEffect, useState } from 'react';
import { AuthResponse, VerifyEmailRequest } from '../../../types/auth';
import ajax from '../../services/ajax';

const EmailVerification = () => {
    const [status, setStatus] = useState<'loading' | 'success' | 'error'>('loading');
    const [message, setMessage] = useState('');

    useEffect(() => {
        const verifyEmail = async () => {
            const urlParams = new URLSearchParams(window.location.search);
            const token = urlParams.get('token');

            if (!token) {
                setStatus('error');
                setMessage('Invalid verification link. Please check your email and try again.');
                return;
            }

            try {
                const response = await ajax.post<AuthResponse>('/auth/verify-email', {
                    token,
                } as VerifyEmailRequest);

                setStatus('success');
                setMessage(response.message || 'Your email has been verified successfully!');
            } catch (error: any) {
                setStatus('error');
                setMessage(error?.message || 'Email verification failed. Please try again.');
            }
        };

        verifyEmail();
    }, []);

    return (
        <div className="min-h-screen flex items-center justify-center bg-gray-50 p-4">
            <div className="max-w-md w-full bg-white rounded-2xl shadow-xl p-8 text-center">
                {status === 'loading' && (
                    <>
                        <div className="w-16 h-16 bg-blue-100 rounded-full mx-auto mb-6 flex items-center justify-center">
                            <div className="animate-spin w-8 h-8 border-2 border-blue-600 border-t-transparent rounded-full"></div>
                        </div>
                        <h2 className="text-2xl font-bold text-gray-900 mb-4">Verifying Email</h2>
                        <p className="text-gray-600">Please wait while we verify your email address...</p>
                    </>
                )}

                {status === 'success' && (
                    <>
                        <div className="w-20 h-20 bg-green-100 rounded-full mx-auto mb-6 flex items-center justify-center">
                            <span className="text-green-600 text-3xl">✓</span>
                        </div>
                        <h2 className="text-2xl font-bold text-gray-900 mb-4">Email Verified!</h2>
                        <p className="text-gray-600 mb-6">{message}</p>
                        <p className="text-gray-600 mb-6">
                            Your account is now activated and ready to use. You can sign in with your credentials.
                        </p>
                        <Button
                            href="/login"
                            className="w-full bg-linear-to-r from-green-600 to-blue-600 hover:from-green-700 hover:to-blue-700 text-white py-3 rounded-lg font-semibold transition-all duration-200"
                        >
                            Continue to Sign In
                        </Button>
                    </>
                )}

                {status === 'error' && (
                    <>
                        <div className="w-20 h-20 bg-red-100 rounded-full mx-auto mb-6 flex items-center justify-center">
                            <span className="text-red-600 text-3xl">✗</span>
                        </div>
                        <h2 className="text-2xl font-bold text-gray-900 mb-4">Verification Failed</h2>
                        <p className="text-gray-600 mb-6">{message}</p>
                        <div className="space-y-3">
                            <Button
                                href="/register"
                                className="w-full bg-linear-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white py-3 rounded-lg font-semibold transition-all duration-200"
                            >
                                Try Registration Again
                            </Button>
                            <Button
                                href="/login"
                                className="w-full text-gray-600 hover:text-gray-700 bg-gray-100 hover:bg-gray-200 py-3 rounded-lg font-medium transition-colors"
                            >
                                Back to Sign In
                            </Button>
                        </div>
                    </>
                )}
            </div>
        </div>
    );
};

export default EmailVerification;
